  margin: 0;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
}

header {
  opacity: .7;
  background-color: #565353;
  margin: 0;
}

ul {
  justify-content: center;
  display: flex;
}

a {
  text-decoration: none;
}

li {
  opacity: .7;
  background-color: #cecece;
